import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CourseService, Course , Comment} from '../shared/course.service';
import { Observable } from 'rxjs';
@Component({
  selector: 'app-course-detail',
  templateUrl: './course-detail.component.html',
  styleUrls: ['./course-detail.component.scss']
})

export class CourseDetailComponent implements OnInit {
course: Course;
comments: Comment[];
public newComment = '';

@Input()

  public isCommentHidden = true;
  public   isShow = true;
  constructor(
    private routeInfo: ActivatedRoute,
    private courseService: CourseService
    ) { }

  ngOnInit() {

    // tslint:disable-next-line:no-string-literal
    const courseId: number = this.routeInfo.snapshot.params['courseId'];
    this.course = this.courseService.getCourse(courseId);
    this.comments = this.courseService.getCommentsForCourseId(courseId);
  }
addComment() {

  const comment = new Comment(0, this.course.id, new Date().toISOString(), '大力', this.newComment);
  this.comments.unshift(comment);
  this.newComment = null;
  this.isCommentHidden = true;
}

}
